import { useTranslation } from 'react-i18next'
// Importamos o hook

const Translator = ({ path }) => {
  const { t } = useTranslation() // Funo que traduz

  // Retornamos a funo passando como parametro o caminho (path)
  // de onde est localizado o texto que desejamos traduzir
  return t(path)
}

export default Translator