// Importando as dependncias
import i18n from 'i18next'
import LanguageDetector from 'i18next-browser-languagedetector'
import { initReactI18next } from 'react-i18next'

// NO PRXIMO PASSO EU MOSTRO AS TRADUES
// Buscando as nossas tradues da pasta locales (nome e local da pasta  voc quem decide)
import translations from './locales'

// Configurao i18n
const i18nConfig = {
  resources: translations,  // resources so as nossas tradues
  fallbackLng: 'pt-BR',     // fallbackLng  o idioma padro caso o browser no consiga detectar sozinho
  defaultNS: 'translations' // defaultNS  o namespace padro, podemos usar 'translations'
}

// No vou entrar no assunto namespaces, nem em configuraes mais complexas
// O objetivo  simplicidade

i18n
  .use(LanguageDetector) // Usa o detector de idioma do seu browser
  .use(initReactI18next) // Usa o pacote do i18n especfico para React
  .init(i18nConfig) // Usa nossas configuraes

export default i18n